body {
  margin: 0;
  padding: 0px;
  border: none;
  height: 90vh;
  
  font-family:'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  --azul-um: #2F7994;
  --azul-dois: #AFE6FA;
  --azul-tres: #5EBEE0;
  --marrom: #9E8B77;
  --laranja: #E0A96E; 

  background-color: black;
  color:white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  height: 100vh;
  width: 100%;
  margin: 0;
  padding: 0px;
  border: none;
  display: flex;
  flex-direction: column;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



.NavigatorBar{
    width: 100%;
    background-color: var(--laranja);
    display: flex;
}

.Link {    
    flex-grow: 1;
    flex-wrap: wrap;
    align-items: end;
    margin: 0px 1px;
    color: black;
    font-weight: bold;
    text-decoration: none;
    border-bottom: solid 3px  rgba(128, 128, 128, 0.223);
    transition: 0.3s;
}

.Link:hover {
    transition: 0.3s;
    border-bottom: solid 3px  var(--azul-dois);
}

.FigureLogo{
    background-color: black;
    padding: 10px;
    border-radius: 50%;
    margin: 4px;
}

.IconLogo{
    font-size: 1.5rem;
    color: var(--azul-tres);
    transition: 0.3s;
}

.IconLogo:hover{
    transition: 0.3s;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    color: var(--azul-dois);
}

.LeftNav{
    flex-grow: 4;
    display: flex;
    justify-content: space-around;
    align-items: end;
}

.RightNav{
    flex-grow: 1;
    display: flex;
    justify-content: space-around;
    align-items: end;
}
.ProfilePage{
    color: wheat;
}
.GenericButton{
    background-color: var(--azul-tres);
    border: solid 1px var(--azul-dois);
    border-radius: 4px;
    padding: 6px;
    font-weight: bold;
    font-size: 1rem;
    transition: 0.5s;
}

.GenericButton:hover{
    background-color: var(--azul-dois);
    border: solid 1px var(--azul-tres);
    transition: 0.3s;
    cursor: pointer;
}
.LogoutPage{
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
}

.MainPage{
    color: aliceblue;
}
.Homepage{
    border: solid 1px var(--laranja);
    margin-top: 10px;
    padding: 5%;
    color: darkgray;
}
.GenericInput {
    background-color: rgba(0, 0, 0, 0);

    border-top: 0ch;
    border-right: 0ch;
    border-left: 0ch;
    border-bottom: 1px solid #c3c3c3;

    color: #c3c3c3;
}

.LoginPage{
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.LoginSection{
    display: flex;
    flex-direction: column;
    width: 40%;
    height: 50%;
    border: solid 1px var(--laranja);
    border-radius: 4px;
    margin-top: 5%;
}

.LoginTitle{
    font-size: 2rem;
    background-color: var(--azul-dois);
    border-bottom: 2px solid var(--laranja);
    color: black;
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.LoginForm{
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
}

.LoginFormRow{
    margin-top: 5%;
    display: flex;
    grid-gap: 5%;
    gap: 5%;
    justify-content: center;
}

.LoginButton{
    background-color: var(--azul-tres);
    border: solid 1px var(--azul-dois);
    border-radius: 4px;
    padding: 6px;
    font-weight: bold;
    font-size: 1rem;
    transition: 0.5s;
    width: 50%;
}

.LoginButton:hover{
    background-color: var(--azul-dois);
    border: solid 1px var(--azul-tres);
    transition: 0.3s;
}

.LinkPassword{
    color: var(--azul-dois);
    font-size: 0.8rem;
    color: var(--laranja);
}

.LoginButtonGoogle{
    background-color: var(--azul-um);
    border: solid 1px var(--azul-dois);
    border-radius: 4px;
    padding: 6px;
    font-weight: bold;
    font-size: 1rem;
    transition: 0.5s;
}
.RegisterPage {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RegisterSection{
    display: flex;
    flex-direction: column;
    width: 40%;
    height: 50%;
    border: solid 1px var(--laranja);
    border-radius: 4px;
    margin-top: 5%;
}

.RegisterTitle{
    font-size: 2rem;
    background-color: var(--azul-dois);
    border-bottom: 2px solid var(--laranja);
    color: black;
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.RegisterForm{
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
}

.RegisterFormRow{
    margin-top: 5%;
    display: flex;
    justify-content: space-between;
    padding: 0px 10%;
}

.Input{
    width: 60%;
    height: 1.5rem;
}

.RegisterButton{
    background-color: var(--azul-tres);
