.BooksPage{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.AddBooksFooter{
    width: 100%;
}
.BooksList{
    width: 100%;

    display: flex;
    flex-direction: column;
    border: solid 1px var(--laranja);
    border-radius: 4px;
    margin-top: 2%;
}

.PanelContainer{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 50%;
    border: solid 1px var(--laranja);
    border-radius: 4px;
    margin-top: 5%;
}

.PanelTitle{
    font-size: 2rem;
    background-color: var(--azul-dois);
    border-bottom: 2px solid var(--laranja);
    color: black;
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Right{
    justify-content: end;
}

.AddIcon{
    color: rgb(32, 202, 32);
    margin: 4px;
    font-size: 2rem;
}

.AddIcon:hover{
    color: greenyellow;
    cursor: pointer;
}

.PanelContent{
    display: flex;
    flex-direction: column;
    padding: 2%;
}
.GenericList{
    border: 1px solid var(--laranja);
}

.GenericListHeader{
    background-color: var(--azul-dois);
    font-weight: bold;
    color:black;
    display: flex;
}

.FieldList{
    overflow: hidden;
    flex-grow: 1;
    border: 1px solid #c3c3c3;
}

.GenericListRow{
    display: flex;
}
.AddBooksPage{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.AddBooksSection{
    display: flex;
    flex-direction: column;
    width: 40%;
    border: solid 1px var(--laranja);
    border-radius: 4px;
    margin-top: 2%;
}

.AddBookFormRow{
    margin-top: 2%;
    display: flex;
    justify-content: space-between;
    padding: 0px 10%;
}
.Root{
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

.Row{
    display: flex;
}

.Column{
    display: flex;
    flex-direction: column;
}

.Center{
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    text-align: center;
}

.CategoryList{
    width: 80%;
    display: flex;
    flex-direction: column;
}

.CategoryRow{
    display: flex;
    justify-content: space-around;
    border: 1px solid var(--marrom);
    align-items: center;
}

.TitleRow{
    background-color: var(--azul-dois);
    font-weight: bold;
    color:black;
}

.FieldCategory{
    width: 50%;
}

.TrashIcon{
    color: red;
}

.TrashIcon:hover{
    color: rgb(254, 66, 66);
